  background-color: #ffffff;
}
.page-body--greenline::before {
  content: "";
  position: absolute;
  top: 1951px;
  left: 50%;
  z-index: -3;
  width: 50%;
  height: 2px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .page-body--greenline::before {
    top: 2597px;
    left: 0;
    min-width: 768px;
  }
}
@media (min-width: 1440px) {
  .page-body--greenline::before {
    top: 1517px;
    min-width: 1440px;
  }
  .page-body--right::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    z-index: -3;
    width: 100%;
    height: 696px;
    background-repeat: no-repeat;
    background-image: linear-gradient(to right, #ffffff 50%, #68b738 50%);
  }
  .page-body--right::before {
    position: absolute;
    content: "";
