.radio {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 320px;
  min-width: 157px;
  margin: 0;
  padding: 0;
  border: none;
}
.radio__wrap-wrap {
  margin: 0;
  padding: 0;
  padding-top: 27px;
  padding-left: 65px;
  border-bottom: 1px solid rgba(1, 1, 1, 0.1);
}
.radio__label {
  position: relative;
  width: 200px;
  height: 30px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 18px;
  text-align: left;
  text-transform: uppercase;
  color: #222222;
}
.radio__label--circle::before {
  content: "";
  position: absolute;
  top: -4px;
  left: -45px;
  display: block;
  z-index: 10;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #cdcdcd;
}
.radio__label--circle:hover::before {
  content: "";
  position: absolute;
  top: -4px;
  left: -45px;
  display: block;
  z-index: 10;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #bababa;
}
.radio__label--circle:focus::before,
.radio__label--circle:active::before {
  opacity: 0.3;
  border: 1px solid #bababa;
}
/* когда у нашего радио-инпута состояние чект, то есть стоит кружок,
то у нас должен бы на нём псевдоэлемента афтер*/
.radio__input:checked + .radio__label--circle::after {
  content: "";
  position: relative;
  top: -17px;
  left: -37px;
  display: block;
  z-index: 10;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: #68b738;
}
.radio__input:checked + .radio__label--circle:focus::after,
.radio__input:checked + .radio__label--circle:active::after {
  opacity: 0.3;
}
.radio__wrap {
  display: block;
  height: 42px;
}
