  padding: 0;
  font-size: 24px;
  font-weight: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
.example__kg-text {
  position: absolute;
  top: 34px;
  left: 18px;
  z-index: 1;
  display: block;
  width: 87px;
  height: 30px;
  padding-left: 3px;
  padding-right: 8px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
