  padding: 0;
  font-size: 24px;
  font-weight: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
.example__time-text {
  position: absolute;
  top: 34px;
  left: 18px;
  z-index: 1;
  display: block;
  width: 87px;
  height: 25px;
  padding-left: 3px;
  padding-right: 3px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  color: #444444;
  background-color: #eaeaea;
}
.example__money {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 220px;
  height: 20px;
  margin-left: 46px;
  margin-top: 33px;
  margin-bottom: 34px;
  padding-left: 19px;
}
.example__money-text {
  width: 168px;
  height: 30px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}
.example__money-rub {
  margin: 0;
  padding: 0;
  width: 100px;
  height: 30px;
  font-size: 14px;
  line-height: 20px;
}
.example__second-part {
  width: 320px;
  margin: 0 auto;
}
.example__cat-picture {
  width: 320px;
  height: 232px;
  margin-left: -1px;
}
.example__wrap-scale {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 50px;
  margin-top: 35px;
  justify-content: left;
}
.example__text-was {
  display: block;
  width: 53px;
  height: 30px;
  margin-left: 56px;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  border: none;
}
.example__scale {
  position: relative;
  display: block;
  width: 90px;
  height: 15px;
  margin-top: 7px;
  margin-left: 7px;
}
.example__scale--body::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 4px;
  width: 84px;
  height: 14px;
  border: 1px solid #cdcdcd;
  border-radius: 6px;
  background-color: #ffffff;
}
.example__scale--body::after {
  position: absolute;
  content: "";
  top: 5px;
  left: 10px;
  width: 35px;
  height: 6px;
  z-index: 10;
  margin: 0;
  padding: 0;
  border-radius: 6px;
  background-color: #68b738;
}
.example__text-is {
  display: block;
  width: 55px;
  height: 30px;
  padding-left: 13px;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  border: none;
}
